/* ==========================================================================
   WooCommerce Product List page styles - begin
   ========================================================================== */

.mkdf-pl-main-holder,
.related.products,
.upsells.products,
.cross-sells,
.mkdf-content .woocommerce {
    @include mkdfRelativeHolderLayout();
}

.mkdf-pl-main-holder {
    margin: 0 0 6px;
    
    .mkdf-woo-pl-info-on-image-hover & {
        margin: 0 0 50px;

        ul.products > .product .mkdf-pl-inner .mkdf-pl-text-inner{
            background-color: rgba($default-heading-color, .4);
        }
    }
}

ul.products {
    list-style: none;
	margin: 0 -15px;
    clear: both;
    
    > .product {
        position: relative;
        display: inline-block;
	    vertical-align: middle;
	    float: left;
	    margin: 0 0 30px;
	    padding: 0 15px;
	    box-sizing: border-box;
        
        .touch & {
            cursor: pointer;
        }
        
        &:hover {
            
            .mkdf-pl-inner .mkdf-pl-text {
                opacity: 1;

                .add_to_cart_button,
                .added_to_cart{
                    bottom: -1px;
                    @include mkdfTransition(all .3s ease);
                }
            }
        }
        
        .mkdf-product-list-title {
            margin: 0 0 7px;
        }
        
        .mkdf-pl-rating-holder {
            @include mkdfRelativeHolderLayout();
            line-height: 1em;
            margin: 0;
        }
        
        .price {
            @include mkdfWooCommercePriceStyle();
        }
        
        .button,
        .added_to_cart {
            margin: 25px 0 0;
    
            .mkdf-woo-pl-info-below-image & {
                margin: 0;
            }
        }
        
        .mkdf-pl-inner {
            @include mkdfRelativeHolderLayout();
            overflow: hidden;
            
            .mkdf-pl-image {
                @include mkdfRelativeHolderLayout();
                overflow: hidden;
                
                img {
                    display: block;
                    width: 100%;
                }
            }
            
            .mkdf-pl-text {
                @include mkdfAbsoluteHolderLayout();
                padding: 0;
                opacity: 0;
                box-sizing: border-box;
                @include mkdfTransition(opacity .2s ease-out);
            }
            
            .mkdf-pl-text-outer {
                @include mkdfTableLayout();
                text-align: center;
            }
            
            .mkdf-pl-text-inner {
                @include mkdfTableCellLayout();
                
                .mkdf-product-list-title,
                .price,
                .star-rating,
                .mkdf-pl-category{
                    color: #fff
                }
                
                .price del {
                    color: rgba(#fff, .8);
                }

                .add_to_cart_button,
                .added_to_cart {
                    width: 100%;
                    display: block;
                    bottom: -70px;
                    @include mkdfTransition(all .2s ease);
                    font-family: $default-heading-font;
                    font-size: 19px;
                    font-weight: 800;
                    font-style: italic;
                    position: absolute;
                    
                    &.added{
                        display: none;
                    }
                }

                .add_to_cart_button{
                    &:before{
                        content: "\f07a";
                        font-family: "FontAwesome";
                        font-size: 20px;
                        left: 0;
                        position: relative;
                        padding-right: 10px;
                    }
                }
            }
            
            > a {
                @include mkdfAbsoluteHolderLayout();
            }
        }
        
        .mkdf-pl-text-wrapper {
            @include mkdfRelativeHolderLayout();
            margin: 17px 0 26px;

            >*{
                line-height: 1.3em;
                display: inline-block;

                &:nth-child(2n+1){
                    float: left;
                    text-align: left;
                    width: 55%;
                }
                &:nth-child(2n){
                    float: right;
                    text-align: right;
                    width: 45%;
                }
            }
        }

        .mkdf-pl-category{
            font-weight: 400;
            margin: 0;
        }
    }
}

$columns_space_label: ('no', 'tiny', 'small', 'normal', 'medium', 'large');
$columns_space_width: (0, 5, 10, 15, 20, 25);

@for $i from 0 to length($columns_space_label) {
    &.mkdf-woo-#{nth($columns_space_label,$i+1)}-space {
        $column_width: nth($columns_space_width,$i+1);
        $column_width_margin: $column_width * 2;
        
        ul.products {
            @if($column_width == 0) {
                margin: 0;
            } @else {
                margin: 0 -#{$column_width}px;
            }
            
            > .product {
                padding: 0 #{$column_width}px;
                margin: 0 0 #{$column_width_margin}px;
            }
        }
    }
}

.mkdf-pl-main-holder,
.related.products,
.upsells.products,
.cross-sells {
    
    $product_list_columns: ('columns-3', 'columns-4');
    
    @for $i from 0 to length($product_list_columns) {
        $column_width: 100%/($i+3);
        
        > .products {
            
            > .product {
                
                @media only screen and (min-width: $ipad-landscape-plus-pixel) {
                    
                    .mkdf-woocommerce-#{nth($product_list_columns,$i+1)} & {
                        width: $column_width;
                        
                        &:nth-child(#{$i+3}n+1) {
                            clear: both;
                        }
                    }
                }
            }
        }
    }
}

.woocommerce {
    
    &.columns-1 {
        
        > .products {
            
            > .product {
                float: none;
                width: 100%;
            }
        }
    }
    
    $product_list_vc_columns: ('columns-2', 'columns-3', 'columns-4', 'columns-5', 'columns-6');
    
    @for $i from 0 to length($product_list_vc_columns) {
        &.#{nth($product_list_vc_columns,$i+1)} {
            $column_vc_width: 100%/($i+2);
            
            > .products {
                
                > .product {
                    width: $column_vc_width;
                    $product_list_vc_columns_label: #{nth($product_list_vc_columns,$i+1)};
                    
                    @if $product_list_vc_columns_label == 'columns-5' {
                        @media only screen and (min-width: $laptop-landscape-plus-pixel) {
                            
                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    } @else if $product_list_vc_columns_label == 'columns-6' {
                        @media only screen and (min-width: $laptop-landscape-large-plus-pixel) {
                            
                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    } @else if $product_list_vc_columns_label == 'columns-3' {
                        @media only screen and (min-width: $ipad-portrait-plus-pixel) {
                            
                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    } @else if $product_list_vc_columns_label == 'columns-2' {
                        @media only screen and (min-width: $phone-landscape-plus-pixel) {
                            
                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    } @else {
                        @media only screen and (min-width: $ipad-landscape-plus-pixel) {
                            
                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    }
                }
            }
        }
    }
}
/* ==========================================================================
   WooCommerce Product List page styles - end
   ========================================================================== */