/* ==========================================================================
   WooCommerce Dropdown Cart styles - begin
   ========================================================================== */

.mkdf-shopping-cart-holder {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    height: 100%;
    padding: 0 18px;

    .touch & {
        cursor: pointer;
    }

    .mkdf-shopping-cart-inner {
        position: relative;
        display: table;
        height: 100%;
    }

    .mkdf-header-cart {
        position: relative;
        display: table-cell;
        vertical-align: middle;

        .mkdf-cart-icon {
            position: relative;
            display: inline-block;
            vertical-align: middle;
        }

        &.mkdf-header-cart-icon-pack {
            .mkdf-cart-icon {
                font-size: 20px;
                line-height: 1em;

                * {

                    display: block;
                    line-height: inherit;

                    &:before {
                        display: block;
                        line-height: inherit;
                    }
                }

                .mkdf-cart-number{
                    position: absolute;
                    top: -8px;
                    right: -8px;
                    background-color: $first-main-color;
                    -webkit-border-radius: 50%;
                    -moz-border-radius: 50%;
                    border-radius: 50%;
                    font-size: 12px;
                    font-family: $default-heading-font;
                    font-weight: 600;
                    width: 17px;
                    height: 17px;
                    line-height: 17px;
                    text-align: center;
                    color: $default-heading-color;
                }
            }
        }

        &.mkdf-header-cart-svg-path {
            .mkdf-cart-icon {

                >* {
                    vertical-align: middle;
                }
                
                > *, 
                path {
                    fill: currentColor;
                    stroke: currentColor;
                }
            }
        }
    }

    &:hover {

        .mkdf-header-cart{
            color: $default-heading-color;
        }

        .mkdf-shopping-cart-dropdown {
            top: 100%;
            opacity: 1;
            visibility: visible;
        }
    }
}

.mkdf-dark-header .mkdf-page-header > div:not(.mkdf-sticky-header):not(.fixed) .mkdf-shopping-cart-holder .mkdf-header-cart {
    color: $header-dark-color;

    &:hover {
        color: $header-dark-hover-color;
    }
}

.mkdf-light-header .mkdf-page-header > div:not(.mkdf-sticky-header):not(.fixed) .mkdf-shopping-cart-holder .mkdf-header-cart {
    color: $header-light-color;

    &:hover {
        color: $header-light-hover-color;
    }
}

.mkdf-shopping-cart-dropdown {
    position: absolute;
    top: calc(100% + 50px);
    left: auto;
    right: 0;
    width: 300px;
    background-color: $default-heading-color;
    text-align: left;
    z-index: 1000;
    opacity: 0;
    visibility: hidden;
    box-sizing: border-box;
    @include mkdfTransition(all 0.35s cubic-bezier(.53,.01,.18,1));

    ul {
        margin: 30px;
        padding: 0;

        li {
            @include mkdfTableLayout();
            padding: 5px 0;
            box-sizing: border-box;

            &.mkdf-empty-cart {
                border-bottom: 0;
                color: #fff;
            }

            &:first-child {
                padding-top: 0;
            }

            &:last-child {
                padding-bottom: 0;
            }
        }
    }

    .mkdf-item-image-holder {
        position: relative;
        display: table-cell;
        height: 100%;
        width: 84px;
        vertical-align: top;

        img {
            display: block;
        }
    }

    .mkdf-item-info-holder {
        @include mkdfTableCellLayout();
        vertical-align: middle;
        padding-left: 15px;

        .mkdf-product-title {
            display: block;
            margin: 0;
            padding: 0 12px 0 0;
            box-sizing: border-box;
            font-weight: 800;
            color: #fff;

            &:hover{
                color: $first-main-color;
            }
        }

        .remove {
            position: absolute;
            top: 0;
            right: 0;
	        font-size: 13px;
	        line-height: 1;
            color: $first-main-color;

            &:hover{
                color: #fff;
            }

            span {
                display: block;
                line-height: inherit;

                &:before {
                    display: block;
                    line-height: inherit;
                }
            }
        }

        .mkdf-quantity {
            display: inline-block;
            margin: 0;
            color: #a5a5a5;
        }

        .amount {
            display: inline-block;
            margin: 0;
            color: #a5a5a5;
        }
    }

    .mkdf-cart-bottom {
        padding: 8px 0 0;

        .mkdf-subtotal-holder {
            position: relative;
            display: table;
            width: 100%;
            margin: 0 0 17px;

            > * {
                display: table-cell;
                vertical-align: top;
                color: #a5a5a5;
                line-height: 20px;
            }

            .mkdf-total-amount {
                text-align: right;
	            font-size: 16px;
            }
        }

        .mkdf-my-cart,
        .mkdf-checkout{
            width: auto;
            display: inline-block;
            height: 42px;
            line-height: 38px;
            font-size: 12px;
            font-weight: 500;
            color: #fff;
            text-align: center;
            box-sizing: border-box;
            @include mkdfTransition(all .2s ease-out);
            letter-spacing: 0;
            border: 2px solid transparent;
        }

        .mkdf-my-cart{
            background-color: $first-main-color;
            color: $default-heading-color;
            margin-right: 8px;
            padding: 0 15px;

            &:hover {
                color: #fff;
                border-color: $first-main-color;
                background-color: transparent;
            }
        }

        .mkdf-checkout{
            border-color: $first-main-color;
            background-color: transparent;
            padding: 0 20px;

            &:hover {
                color: $default-heading-color;
                background-color: $first-main-color;
            }
        }
    }
}

.mkdf-vertical-menu-area {

    .mkdf-shopping-cart-dropdown {
        display: none !important;
    }
}
/* ==========================================================================
   WooCommerce Dropdown Cart styles - end
   ========================================================================== */