/* ==========================================================================
   Marks (Sale, Out of Stock) mixin styles - begin
   ========================================================================== */

@mixin mkdfWooCommerceMarksGeneralStyle() {
    position: absolute;
    display: block;
    top: 0;
    width: auto;
    height: 39px;
    margin: 0;
    padding: 0 30px;
    color: #fff;
    font-size: 15px;
    font-weight: 800;
    font-style: italic;
    font-family: $default-heading-font;
    line-height: 39px;
    letter-spacing: 1px;
    text-align: center;
    z-index: 100;
    box-sizing: border-box;
    @include mkdfTransform(translateZ(0));
    text-transform: uppercase;
}

@mixin mkdfWooCommerceSaleStyle() {
    right: 0;
    background-color: $default-heading-color;
}

@mixin mkdfWooCommerceOutOfStockStyle() {
    right: 0;
    background-color: $default-heading-color;
}

@mixin mkdfWooCommerceNewProductStyle() {
    right: 0;
    background-color: $second-main-color;
}

/* ==========================================================================
   Marks (Sale, Out of Stock) mixin styles - end
   ========================================================================== */

/* ==========================================================================
   Price mixin styles - begin
   ========================================================================== */

@mixin mkdfWooCommercePriceStyle() {
    position: relative;
    display: block;
    margin: 0 0 7px;
    line-height: 1;
    color: $default-text-color;
    
    * {
        font-size: inherit;
        line-height: inherit;
        font-style: italic;
    }
    
    del {
        margin: 0 4px 0 0;
        color: rgba($default-heading-color, .8);
    }
    
    ins {
        text-decoration: none;
    }
}
/* ==========================================================================
   Price mixin styles - end
   ========================================================================== */

/* ==========================================================================
   Star Ratings mixin styles - begin
   ========================================================================== */

%star-ratings {
    font-family: "FontAwesome";
    font-size: 12px;
    line-height: 1;
    letter-spacing: 1px;
    color: $default-text-color;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

@mixin mkdfWooCommerceRatingsStyle() {
    position: relative;
    display: inline-block;
    vertical-align: baseline;
    @extend %star-ratings;
    overflow: hidden;
    
    &:before {
        content: "\f006\f006\f006\f006\f006";
    }
    
    span {
        position: absolute;
        top: 0;
        left: 0;
        overflow: hidden;
        
        &:before {
            content: "\f005\f005\f005\f005\f005";
        }
    }
}

@mixin mkdfWooCommerceSingleProductRatingsStyle() {
    
    &:before {
        content: "\f006";
        position: relative;
        display: inline-block;
        vertical-align: middle;
        @extend %star-ratings;
        overflow: hidden;
        z-index: -1;
    }
    
    &.active:after {
        content: "\f005\f005\f005\f005\f005";
        position: absolute;
        right: 0;
        bottom: 7px;
        display: block;
        @extend %star-ratings;
    }
}
/* ==========================================================================
   Star Ratings mixin styles - end
   ========================================================================== */