/* ==========================================================================
   WooCommerce Pagination styles - begin
   ========================================================================== */
.mkdf-woo-pagination-holder {
    @include mkdfRelativeHolderLayout();
    margin: 0 0 80px;
    clear: both;

    .mkdf-woo-pagination-inner {
        position: relative;
        display: table;
        width: 100%;
        text-align: right;
    }
}

.woocommerce-pagination {
    position: relative;
    display: table-cell;
    vertical-align: top;
    width: 99%;
    margin: 0;
    padding: 0;

    .page-numbers {
        position: relative;
        display: inline-block;
        vertical-align: top;
        width: 100%;
        margin: 0;
        padding: 0;

        li {
            position: relative;
            display: inline-block;
            vertical-align: middle;
            margin: 0 11px;

            a,
            span {
                position: relative;
                display: inline-block;
                vertical-align: middle;
                margin: 0;
                padding: 0;
                font-weight: 800;
                font-style: italic;
                font-family: $default-heading-font;
                font-size: 20px;
                padding-bottom: 6px;

                &:after{
                    content: '';
                    height: 2px;
                    background-color: transparent;
                    width: 100%;
                    position: absolute;
                    bottom: 0;
                    left: 0;
                    @include mkdfTransition(all .3s ease);
                }

                &:hover,
                &.current {
                    color: $default-heading-color;

                    &:after{
                        background-color: $default-heading-color;
                        @include mkdfTransition(all .3s ease);
                    }
                }
            }

            a {

                &.next,
                &.prev {
                    font-size: 0;
                    line-height: 1;
                    margin: 0;

                    &:before {
                        position: relative;
                        top: -1px;
                        display: block;
                        font-family: 'FontAwesome';
                        font-size: 15px;
                        line-height: inherit;
                        -webkit-font-smoothing: antialiased;
                        -moz-osx-font-smoothing: grayscale;
                        font-style: normal;
                    }

                    &:hover{
                        color: $second-main-color;
                    }

                    &:after{
                        display: none;
                    }
                }
    
                &.prev {
        
                    &:before {
                        content: "\f053";
                    }
                }
    
                &.next {
        
                    &:before {
                        content: "\f054";
                    }
                }
            }
        }
    }
}

.mkdf-woo-view-all-pagination {
    position: relative;
    display: table-cell;
    vertical-align: top;
    width: 1%;

    a {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        white-space: nowrap;
        padding: 0 0 0 2px;
        color: $default-text-color;

        &:before {
            content: '|';
            padding: 0 14px 0 0;
            color: $default-text-color;
        }

        &:hover {
            color: $default-heading-color;
        }
    }
}
/* ==========================================================================
   WooCommerce Pagination styles - end
   ========================================================================== */